.field-label{
    font-weight: 800;
      display: flex;
      margin: 10px;
  }
  .onboarding-container{
    max-width: 80%;
      background: #FFFFFF;
      box-shadow: 0px 8px 24px rgb(0 0 0 / 15%);
      border-radius: 8px;
      margin: 20px auto;
      padding: 20px;
  }
  .btn-container{
    display: flex;
    width: 100%;
      justify-content: flex-end;
      margin: 20px;
  }
  .btn-container button{
    
    background: #7F31D2;
    border-radius: 8px;
    color: white;
    padding: 10px 20px;
    font-size: clamp(15px,1.5vw, 25px);
    text-transform: none;
  }
  .upload-btn-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
  }
  
  @media screen and (max-width: 768px){
  
    .btn-container{
      bottom: 0px;
      background: #fff;
      z-index: 2;
      padding: 10px 0px;
      margin: unset;
    }
    .btn-container button{
      width: 100%;
    }
  }
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  